<template>
  <div class="filter-wrapper">

    <div class="filter-item">
        <div class="smalltitle">Distric</div>

        <div>
            <el-checkbox-group v-model="distric" size="medium" class="facilities">
                <el-checkbox  label="xuhui" border></el-checkbox>
                <el-checkbox  label="jingan" border></el-checkbox>
                <el-checkbox  label="changning" border></el-checkbox>
                <el-checkbox  label="putong" border></el-checkbox>
                <el-checkbox  label="huangpu" border></el-checkbox>
          
            </el-checkbox-group>
        </div>
    </div>
      
    <div class="smalltitle">Budget</div>

    
    <div class="filter-item">

        <span class="item-title longtitle">Budget: from ¥ {{budget[0]}} to  ¥ {{ budget[1]}}  </span>
        
                <div class="block">
                <el-slider
                v-model="budget"
                range
                :max=50000>
                </el-slider>
                </div>
    </div>


        <div class="smalltitle">Apartment details</div>


    <div class="filter-item">

        <span class="item-title">Rooms : </span>
    

        <el-input-number v-model="rooms" :min="1" :max="10" label="描述文字"></el-input-number>
    </div>

     <div class="filter-item">
        <span class="item-title">Bath : </span>
        <yd-spinner class="item-value" width="150px" height="40px" v-model="bath" min="1" unit="1" max="3"></yd-spinner>

        <yd-spinner max="75" unit="3" width="150px" height="40px" v-model="spinner1"></yd-spinner>
    </div>



    <div class="filter-item">
        <div class="smalltitle">Facilities</div>

        <div>
            <el-checkbox-group v-model="facilities" size="medium" class="facilities">
                <el-checkbox  label="unfurnished" border></el-checkbox>
                <el-checkbox  label="heating" border></el-checkbox>
                <el-checkbox  label="open kitchen" border></el-checkbox>
                <el-checkbox  label="outdoor space" border></el-checkbox>
                <el-checkbox  label="gym" border></el-checkbox>
                <el-checkbox  label="parking" border></el-checkbox>
                <el-checkbox  label="bathtub" border></el-checkbox>
                <el-checkbox  label="owen" border></el-checkbox>
                <el-checkbox  label="dish washer" border></el-checkbox>

            </el-checkbox-group>
        </div>
    </div>
    
    <yd-button-group>
    <yd-button type="primary" size="large" style="margin-bottom:30px" @click.native="getFiltered">Show the matchs </yd-button>
    </yd-button-group>
  </div>


</template>
<script>
import {mapMutations,mapGetters} from 'vuex'
export default {
    data() {
        return{
            spinner1:0,
            distric:[],
            facilities:[],
            rooms:0,
            bath:0,
            budget:[0,0]
        }
    },
    computed: {
        ...mapMutations(['UPDATEPOP']),
        pop:{
            get(){
                return this.$store.state.pop
            },
            set(){
                this.$store.commit('UPDATEPOP')
            }
        }
    },
    methods: {
        ...mapMutations(['UPDATE_FILTER']),
        getFiltered() {
            this.UPDATE_FILTER()
        }
    }
}
</script>

<style lang="stylus" scoped>
.filter-wrapper
    .filter-item
        margin-top 5px
        .facilities
            .el-checkbox
                margin-bottom 10px
        .block
            margin 0 20px
        .item-options
            text-align left
            height 50px
            line-height 50px
            margin-left 8px
    .item-title
        display inline-block
        line-height 50px
        width 120px
        text-align right  
        margin-right 10px 
    .longtitle
        width 300px
    .item-value
        display inline-block
        vertical-align middle


</style>




